<template>
  <div class="secondary">
    <!-- 头部 -->
    <div class="secondary-header">
      <div class="tit-img">
        <img src="../../assets/images/jingxuanhuichang.jpg" alt />
      </div>
      <ul>
        <li v-for="(item,index) in list" :key="index">
          <div class="header-img">
            <img :src="item.url" alt />
          </div>
          <span>至高24期免息</span>
          <div>{{item.title}}</div>
        </li>
      </ul>
    </div>

    <div class="rotation">
      <div class="swiper-container rotation-swiper">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="(item,index) in secondaryrotation" :key="index">
            <img :src="item.url" />
          </div>
          <div></div>
        </div>
      </div>
    </div>

    <div class="secondary-container">
      <div class="top-container">
        <div>
          <div
            class="top-title"
            :style="{background:'linear-gradient(90deg, #45CAFF,#1471FB)', '-webkit-background-clip':'text', color: 'transparent'}"
          >职场精英</div>
          <span>成为同事们关注的焦点</span>
          <div class="top-img">
            <img src="../../assets/images/hometuijian01.jpg" alt />
            <img src="../../assets/images/diannao04.jpg" alt />
          </div>
        </div>
        <div class="container-right">
          <div
            class="top-title"
            :style="{background:'linear-gradient(90deg, #45CAFF,#1471FB)', '-webkit-background-clip':'text', color: 'transparent'}"
          >引领潮流</div>
          <span>遇见春光</span>
          <div class="top-img">
            <img src="../../assets/images/hometuijian01.jpg" alt />
            <img src="../../assets/images/diannao05.jpg" alt />
          </div>
        </div>
      </div>
    </div>
    <div class="secondary-recommended">
      <hx-Recommended></hx-Recommended>
    </div>
  </div>
</template>

<script>
import Swiper from "swiper";
import hxRecommended from "../../components/hxRecommended";

export default {
  data() {
    return {
      list: [],
      activeName: "",
      secondaryrotation: [
        { url: require("../../assets/images/secondaryrotation01.jpg") },
        { url: require("../../assets/images/secondaryrotation02.jpg") },
        { url: require("../../assets/images/secondaryrotation03.jpg") },
        { url: require("../../assets/images/secondaryrotation04.jpg") },
        { url: require("../../assets/images/secondaryrotation05.jpg") },
        { url: require("../../assets/images/secondaryrotation06.jpg") }
      ]
    };
  },
  created() {
    this.list = this.$store.state.home.list[0];
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
    // console.log(this.$route.query.activeName)
    this.activeName = this.$route.query.activeName;
    // console.log(this.activeName);
    if (this.$route.path === "/home/secondary") {
      this.$store.state.num = 0;
    }
  },
  watch: {
    $route(to) {
      // console.log(to.query.activeName);
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
      this.list = this.$store.state.home.list[to.query.activeName - 1];
    }
  },
  mounted() {
    new Swiper(".rotation-swiper", {
      slidesPerView: 3,
      spaceBetween: 30,
      centeredSlides: true,
      loop: true,
      autoplay: true
    });
  },
  components: {
    hxRecommended
  }
};
</script>

<style lang="less" scoped>
.secondary {
  width: 100%;
  height: 100%;
  background-color: #f6f6f6;
  .secondary-header {
    width: calc(100% - 20px);
    margin-left: 8px;
    height: 290px;
    margin-top: 20px;
    .tit-img {
      img {
        width: 100%;
      }
    }
    ul {
      overflow: hidden;
      width: 100%;
    }
    li {
      width: calc(25% - 4px);
      height: 118px;
      background-image: url(../../assets/images/jingxuanbg.jpg);
      background-size: 100%;
