      margin-left: 4px;
      border-radius: 5px;
      margin-top: 7px;
      text-align: center;
      .header-img {
        width: 58px;
        height: 60px;
        margin-left: 13px;
        margin-top: 10px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      span {
        font-size: 10px;
        color: #fff;
        position: relative;
        bottom: 2px;
      }
      div {
        color: #fff;
      }
    }
  }

  .secondary-container {
    height: 150px;
    width: calc(100% - 20px);
    margin: 10px 10px 10px 10px;
    overflow: hidden;
    border-radius: 10px;
    .top-container {
      width: 100%;
      height: 100%;
      margin-bottom: 10px;
      > div {
        width: calc(50% - 1px);
        height: 100%;
        float: left;
        background-color: #fff;
        .top-title {
          font-weight: bold;
          padding-left: 10px;
        }
        span {
          font-size: 12px;
          padding-left: 10px;
          color: #666771;
        }
