            margin: 10px 10px 0 10px;
          }
        }
      }
      .container-right {
        margin-left: 2px;
      }
    }
  }
  .rotation {
    width: calc(100% - 60px);
    height: 110px;
    margin-top: 20px;
    margin-left: 30px;
    .swiper-container {
      width: 100%;
      height: 90px;
    }
    .swiper-slide {
      text-align: center;
      font-size: 18px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      -webkit-justify-content: center;
      justify-content: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      -webkit-align-items: center;
      align-items: center;
      transition: 300ms;
      transform: scale(0.8);
      position: relative;
      width: 120px;
      height: 90px;
      img {
        width: 110px;
        height: 90px;
      }
    }
    .swiper-slide-active,
    .swiper-slide-duplicate-active {
      transform: scale(1);
    }
  }
  .secondary-recommended {
    margin-bottom: 50px;
  }
